import React from 'react';
import { ajax } from '../../services/ajax';

export class EmailForm extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            email: '',
            isEmailValid: false,
            isSending: false,
        };

        this.submitForm = this.submitForm.bind(this);
    }

    emailHandler(event) {
        this.setState({
            email: event.target.value,
            isEmailValid: isValidEmail(event.target.value)
        });

    }

    submitForm() {
        if(!document.getElementById('gdpr').checked) {
            alert('You have to accept our privacy policy.')
            return;
        }

        if(this.state.isEmailValid) {
            this.setState({
                isSending: true
            });

            ajax('https://j21bzza060.execute-api.eu-central-1.amazonaws.com/prod/email', 'POST' ,{email: this.state.email}).then(
                    (success) => {
                        alert(success.message);
                        this.setState({
                            isSending: false
                        });
                    },
                    (error) => {
                        alert(error);
                        this.setState({
                            isSending: false
                        });
                    }
                )
        }
    }

    render() {
        return (
            <section className="contact">
                <span id="email-form" className="anchor"></span>
                <div className="row">
                    <div className="col-12">
                        <div className="email-title">
                            Want to know more...
                        <br /><br />
                            Be the first to get an <span className="engel">ENGEL</span>
                        </div>
                        <form id="email-form" className={!!this.state.email ? (!this.state.isEmailValid ? 'invalid' : 'valid') : ''}>
                            <input id="email" type="email" placeholder="Email" onKeyUp={e => this.emailHandler(e)} />
                            <br />
                            <input type="checkbox" id="gdpr" value="agree" />
                            <label> I accept the <a href="assets/Privacy Policy engel.pdf" target="_blank">privacy policy</a><span className="required">*</span></label>
                            <br />
                            <br />
                            <button
                                disabled={!this.state.isEmailValid || this.state.isSending}
                                id="submit-btn"
                                type="button"
                                onClick={this.submitForm}>
                                    {this.state.isSending ? 'Sending' : 'Submit'}
                            </button>
                        </form>
                    </div>
                </div>
            </section>
        );
    }
}

const isValidEmail = (email) => {
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
};